export default {
  data() {
    return {
      isEdit: false,
      editList: [],
      tempNum: NaN,
    }
  },
  computed: {
    goods() {
      return this.$store.state.shoppingCartList;
    },
    custom() {
      return this.$store.state.custom;
    },
    selectAll() {
      return this.goods.every(val => val.isSelected);
    },
    amount() {
      let sum = 0;
      this.$store.state.shoppingCartList.forEach(val => {
        if(val.isSelected == true) {
          sum += val.quantity * val.price;
        }
      });
      return sum;
    }
  },
  methods: {
    minus({id, quantity}) {
      if(quantity > 1) {
        this.$store.commit('minusQuantity', id);
      }
    },
    plus({id}) {
      this.$store.commit('plusQuantity', id);
    },
    selectToggle({id}) {
      this.$store.commit('selectToggle', id);
    },
    editNum(index) {
      // 编辑数量
      let arr = [];
      arr[index] = true;
      this.editList = arr;
    },
    blur(ev, {id}) {
      // 失去焦点，验证数据
      this.editList = [];
      let newval = parseInt(ev.target.value);
      if(newval == newval && newval > 0 && (newval % 1 == 0)) {
        this.$store.commit('changeQuantity', {id, num: newval})
      }
    },
    selectAllToggle() {
      // 全选，反选
      if(this.selectAll) {
        this.$store.commit('selectAllOrNot', 'none')
      }
      else {
        this.$store.commit('selectAllOrNot', 'all')
      }
      console.log(this.$store.state.shoppingCartList);
      
    }
  },
  directives: {
    focus: {
      inserted(el, binding) {
        el.focus();
        el.value = binding.value;
      }
    }
  }
}



