
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
data() {
return {
banner: [],
business: [],
custom: [],
quality: [],
recommend: [],
}
},
components: {
Swiper,
},
created() {
this.banner = this.$store.state.banner;
this.business = this.$store.state.business;
this.custom = this.$store.state.custom;
this.custom = [...this.custom, ...this.custom, ...this.custom, ...this.custom];
this.quality = this.$store.state.quality;
this.recommend = this.$store.state.recommend;
this.recommend = [...this.recommend, ...this.recommend, ...this.recommend];
}
}



