export default {
  updatePageScrollHistory(state, {page, value}) {
    // page: router
    // value: scroll distance
    state.pageScrollHistory[page] = value;
  },
  plusQuantity(state, id) {
    // 增加数量
    state.shoppingCartList.forEach(val => {
      if(val.id == id) {
        val.quantity++;
      }
    });
  },
  minusQuantity(state, id) {
    // 减少数量
    state.shoppingCartList.forEach(val => {      
      if(val.id == id && val.quantity > 1) {
        val.quantity--;
      }
    });
  },
  deleteQuantity(state, id) {
    // 删除条目
    let delindex = null;
    state.shoppingCartList.forEach((val, index) => {
      if(val.id == id) {
        delindex = index;
      }
    });
    if(delindex) {
      state.shoppingCartList.splice(delindex, 1);
    }
  },
  changeQuantity(state, {id, num}) {
    // 改变数量
    if(num == num && num > 0 && (num % 1 == 0)) {
      state.shoppingCartList.forEach(val => {      
        if(val.id == id) {
          val.quantity = num;
        }
      });
    }  
  },
  selectToggle(state, id) {
    // 改变选中状态
    state.shoppingCartList.forEach(val => {
      if(val.id == id) {
        val.isSelected = !val.isSelected;
      }
    });
  },
  selectAllOrNot(state, type) {
    if(type == 'none') {
      state.shoppingCartList.forEach(val => {
        val.isSelected = false;
      })
    }
    else if(type == 'all') {
      state.shoppingCartList.forEach(val => {
        val.isSelected = true;
      })
    }
  }
}


