import SortBlock from '@/components/SortBlock'
export default {
  components: {
    SortBlock,
  },
  data() {
    return {
      list: [],
      ad: {},
      recmd: [],
      positions: {},
      activePosition: '',
    }
  },
  methods: {
    goto(ev) {
      let target = ev.target;
      if(target.tagName == 'A') {
        let to = this.positions[target.getAttribute('data-name')];
        if(to) {
          this.activePosition = target.getAttribute('data-name');
          // 延迟滚动
          let count = 20;
          let dis = to - window.pageYOffset;
          let timer = setInterval(() => {
            let now = window.pageYOffset;
            window.scroll(0, dis / 20 + now);
            count--;
            if(count == 0) {
              clearInterval(timer);
              timer = null;
            }
          }, 10);
        }        
      }
    },
    judgePlace() {
      if(JSON.stringify(this.positions) != '{}') {
        // 判断是否为空对象
        let now = window.pageYOffset;
        let temp = null;
        for(let key in this.positions) {
          if(now > this.positions[key]) {
            temp = key;
          }
          else {
            break;
          }
        }
        this.activePosition = temp ? temp : this.activePosition;
      }
    },
    scroll() {
      this.judgePlace();      
    },
  },
  created() {
    this.list = this.$store.state.sortList;
    let temp = this.list[0].list;
    this.list.forEach(val => {val.list = temp;});

    this.ad = this.$store.state.sortAd;
    this.recmd = this.$store.state.sortRecommend;
  },
  mounted() {
    // 在加载完成后调用
    setTimeout(() => {
      for(let item of this.list) {
        this.positions[item.name] = this.$refs[item.name][0].$el.offsetTop - 100;
      }

      this.judgePlace();
      document.addEventListener('scroll', this.scroll);
    }, 0);
  },
  beforeDestroy() {
    document.removeEventListener('scroll', this.scroll);    
  }
}



