export default {
  data() {
    return {
      isLoaded: false,
    }
  },
  methods: {
    enterIndex() {
      this.$router.push('/index');
    }
  },
  computed: {
    page() {
      return parseInt(this.$route.params.page);
    }
  },
  mounted() {
    // 设置定时跳转
    if(!this.isLoaded) {
      setTimeout(() => {
        this.isLoaded = true;
        this.$router.push('/guide/1')
      }, 1000);
    } 
  },
  watch: {
    $route: {
      handler(cur, old) {
        if(this.page >= 1 && this.page <= 3) {
          if(this.isLoaded) {
            this.$refs['guide-box'].style.left = -(this.page - 1) * 720 + 'px';
          }
          else {
            this.$router.push('/guide/')
          }
        }
      },
      // immediate: true,
    }
  },
  directives: {
    touchto: {
      bind(el, binding, vnode) {
        let guideBox = el.getElementsByClassName('guide-box')[0];
        let setx = 150;        
        // 手指触摸滑动翻页事件
        // 滑动触发所需要滑过的像素距离，默认值 150px
        el.ontouchstart = (ev) => {
          // ev.preventDefault();
          let ox = ev.touches[0].clientX;
          let ex = 0;
          let curroute =  vnode.context.$router;
          let curpage = parseInt(vnode.context.$route.params.page);

          // 绑定手指滑动事件
          el.ontouchmove = (ev) => {
            ex = ev.touches[0].clientX - ox;
          };

          // 绑定滑动结束事件
          el.ontouchend = (ev) => {
            // 右滑          
            if(ex <= -setx && curpage < 3) {
              curroute.push('/guide/' + (curpage + 1));
            }
            // 左滑
            else if(ex >= setx && curpage > 1) {
              curroute.push('/guide/' + (curpage - 1));
            }
            // 清除滑动事件和滑动结束事件
            el.ontouchmove = null;
            el.ontouchend = null;
          };

        };
      },
    }
  }
}



